* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "Roboto-b";
  src: url("../font/Roboto/bold.ttf");
}
@font-face {
  font-family: "Roboto-r";
  src: url("../font/Roboto/regular.ttf");
}
@font-face {
  font-family: "Roboto-l";
  src: url("../font/Roboto/light.ttf");
}
@font-face {
  font-family: "Roboto-t";
  src: url("../font/Roboto/thin.ttf");
}
section {
  width: 100vw;
  height: 100vh;
}
section .container {
  max-width: 1440px;
  height: 960px;
  margin: 100px auto;
}
section .container .container__content {
  margin: 96px 0;
  display: flex;
  justify-content: center;
}
section .container .container__content .content__right {
  margin: 40px 95px;
  max-width: 380px;
}
section .container .container__content .content__right h1 {
  color: #121212;
  font-family: "Roboto-b";
  font-style: normal;
  font-size: 42px;
  line-height: 120%;
}
section .container .container__content .content__right p {
  color: #121212;
  font-family: "Roboto-l";
  font-style: normal;
  font-size: 16px;
  line-height: 150%;
  margin: 24px 0 32px 0;
}
section .container .container__content .content__right .btn {
  width: 128px;
  height: 48px;
  border-radius: 32px;
  background-color: #E4C1AE;
  text-align: center;
}
section .container .container__content .content__right .btn span {
  color: #fff;
  font-style: normal;
  font-family: "Roboto-b";
  font-size: 16px;
  line-height: 48px;
}